import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const store = new Vuex.Store({
  strict: true,
  state: {
    connected: false,
    users: [],
    messages: [],
    myName: 'Sally Brundy',
    myImage: 'https://randomuser.me/api/portraits/women/32.jpg',
    isTyping: false
  },
  actions: {
    sendMessage (context, message) {
      console.log('new message action', message)
      this._vm.$socket.emit('newMessage', message)
    },
    isTyping (context, status) {
      context.commit('isTyping', status)
      this._vm.$socket.emit('typing')
    },
    setName (context, name) {
      context.commit('setName', name)
      this._vm.$socket.emit('add user', name)
    }
  },
  mutations: {
    setName (state, name) {
      state.myName = name
    },
    isTyping (state, status) {
      state.isTyping = status
    },
    SOCKET_CONNECT (state) {
      state.connected = true
      console.log('socket.io connected')
    },
    SOCKET_DISCONNECT (state) {
      state.connected = false
      console.log('socket.io disconnected')
    },
    SOCKET_NEW_MESSAGE (state, message) {
      console.log('message from the server', message[0])
      state.messages.push(message[0]) // Getting an array back from the server, grabbing first element
    },
    SOCKET_LOGIN (state, data) {
      console.log('login', data)
      state.users = data[0].clients
    }
  }
})

export default store



