<template>
    <aside class="left-bar">
      <article class="profile">
        <img class="profile__img" :src="myImage" alt="">
        <p class="profile__name">{{ myName }}</p>
        <!--<p class="profile__name">Super Long Name You DOn't want</p>-->
      </article>
      <article class="actions">
        <i @click="changeName" class="fas fa-cog fa-2x action__settings"></i>
      </article>
    </aside>
</template>

<script>
export default {
  name: 'LeftBar',
  data () {
    return {
      myName: null,
      myImage: null
    }
  },
  methods: {
    changeName () {
      this.myName = prompt('Name?')
      this.$store.dispatch('setName', this.myName)
    }
  },
  created () {
    this.myName = this.$store.state.myName
    this.myImage = this.$store.state.myImage
  }
}
</script>

<style scoped>
  .left-bar {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    background: #383F51;
    min-width: 120px; /* em rem or px? */
    width: 120px;
    padding-top: 22px;
    text-align: center;
  }
  .profile__img {
    box-sizing: border-box;
    width: 76px; /* px to rem or em */
    border-radius: 50%;
    border: 2px solid white;

  }
  .profile__name {
    margin-top: .6em;
    font-size: .875rem;
    font-weight: 600;
    color: white;
  }
  .actions {
    width: 75%;
    border-top: 1px solid #979797;
    text-align: center;
    height: 73px;
  }
  .action__settings {
    margin-top: 15px;
    font-size: 32px;
    color: white;
  }
  @media only screen and (max-width: 768px) {
    .left-bar {
      display: none;
    }
  }
</style>



