// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import VueSocketio from 'vue-socket.io'
import firebase from 'firebase'
import store from './store/'

Vue.config.productionTip = false

// Initialize Firebase
let firebaseConfig = {
  apiKey: 'AIzaSyDy1PWhzunMRW57Q50yWM3am-ji5rqfUbs',
  authDomain: 'vuechat-81d47.firebaseapp.com',
  databaseURL: 'https://vuechat-81d47.firebaseio.com',
  projectId: 'vuechat-81d47',
  storageBucket: 'vuechat-81d47.appspot.com',
  messagingSenderId: '169575794536'
}

firebase.initializeApp(firebaseConfig)

// Vue.use(VueSocketio, window.location.hostname + ':3000', store)
Vue.use(VueSocketio, window.location.hostname, store)

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



