<template>
    <section class="chat-area">
      <ul class="messages" ref="chatArea">
        <li class="message">
          <img class="message__image" src="https://randomuser.me/api/portraits/women/32.jpg" alt="">
          <article>
            <h3 class="message__author">Sally Brundy</h3>
            <span class="message__time">3:12 PM</span>
            <p class="message__body">I built a little chat app using socket io already, so I thought it would be fun to
              try and replicate it using pusher for the dev.to contest they are doing with
              pusher.</p>
          </article>
        </li>
        <li class="message">
          <img class="message__image" src="https://randomuser.me/api/portraits/women/2.jpg" alt="">
          <article>
            <h3 class="message__author">Sarah Dirks</h3>
            <span class="message__time">3:13 PM</span>
            <p class="message__body">Well that’s super fancy and I love the idea. Let’s work together on this project.</p>
          </article>
        </li>
        <li v-for="message in messages" :key="messages.indexOf(message)" class="message">
          <img class="message__image" src="https://randomuser.me/api/portraits/women/32.jpg" alt="">
          <article>
            <h3 class="message__author">{{ message.author }}</h3>
            <span class="message__time">3:13 PM</span>
            <p class="message__body">{{ message.body }}</p>
          </article>
        </li>
      </ul>
      <form class="chat-area__form" @submit.prevent="sendMessage">
        <input class="chat-area__input" autofocus type="text" v-model="myMessage" placeholder="Type Something...">
      </form>
    </section>
</template>

<script>
export default {
  name: 'ChatArea',
  data () {
    return {
      myMessage: '',
      myName: null,
      messages: null
    }
  },
  watch: {
    myMessage () {
      if (this.myMessage) {
        this.$store.dispatch('isTyping', true)
      } else {
        this.$store.dispatch('isTyping', false)
      }
    },
    messages () {
      console.log('messages changed, trying to scroll up')
      this.$nextTick(() => {
        let messageDisplay = this.$refs.chatArea
        // console.log(messageDisplay.scrollTop)
        // console.log(messageDisplay.scrollHeight)
        messageDisplay.scrollTop = messageDisplay.scrollHeight
      })
    }
  },
  methods: {
    sendMessage () {
      if (!this.myMessage) {
        return
      }
      // console.log('sendMessage(), ', this.myMessage)
      this.$store.dispatch('sendMessage', this.myMessage).then(() => {
        console.log('something')
        this.myMessage = ''
      })
    }
  },
  created () {
    this.myName = this.$store.state.myName
    this.messages = this.$store.state.messages
  }
}
</script>

<style scoped>
  .chat-area {
    background: white;
    width: 100%;
    padding: 32px 24px 0 24px;
    /*overflow: auto;*/
    /*position: relative;*/
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    /*height: 100%;*/
    /* Need to adjust the message margins after adding flexbox */
  }

  .messages {
    padding: 0;
    list-style: none;
    /*height: 100%;*/
    flex: 1;
    overflow-y: auto;
  }

  .message {
    display: flex;
    margin-top: 10px;
  }

  .message__image {
    border-radius: 50%;
    width: 40px;
    height: 40px;
    margin-right: 9px;
  }

  .message__author {
    display: inline;
    font-size: 14px;
    font-weight: bold;
    letter-spacing: 0;
  }

  .message__time {
    font-size: 13px;
    color: #737373;
    font-weight: 400;
    letter-spacing: -0.64px;
  }

  .message__body {
    font-size: 13px;
    font-weight: 500;
    color: #545454;
    margin-top: 4px;
    letter-spacing: .4px;
  }

  .chat-area__form {
    margin-right: 20px;
    margin-bottom: 20px;
    /*background: green;*/
    /*position: fixed;*/
    /*width: 69%;*/
  }

  .chat-area__input {
    border: 2px solid #595959;
    height: 40px;
    font-size: 15px;
    color: black;
    font-weight: 400;
    padding-left: 15px;
    width: 100%;
    outline: none;
  }
</style>



